<script setup lang="ts">
    import { onMounted, ref } from "vue";
    import useFetch from "@/composables/useFetch";
    import ClubBox from "./ClubBox.vue";
    import { getCurrentPosition } from "@/util/common";

    const distance = ref(10);
    const crd = ref<any>(null);

    const emit = defineEmits<{
        fetchList: [p: CloseRangeResDto];
    }>();

    const { data } = useFetch<CloseRangeResDto>({
        url: "/search/close-range",
        enable: () => crd.value,
        dependancy: [() => distance.value, () => crd.value],
        param: () => ({
            klmtr: distance.value,
            xCrvl: crd.value.latitude,
            yCrvl: crd.value.longitude
        }),
        onSuccess: (res: CloseRangeResDto) => {
            emit("fetchList", res);
        }
    });

    onMounted(() => {
        navigator.geolocation.getCurrentPosition(position => {
            crd.value = position.coords;
        });
    });
</script>

<template>
    <div class="inner-w">
        <div class="rgBtns d-flex ai-center">
            <span class="rgBtns-tit"><VIcon icon="ic_location_green" />거리 선택</span>
            <button @click="distance = 10" :class="{ 'is-active': distance === 10 }">10km</button>
            <button @click="distance = 30" :class="{ 'is-active': distance === 30 }">30km</button>
            <button @click="distance = 50" :class="{ 'is-active': distance === 50 }">50km</button>
            <button @click="distance = 70" :class="{ 'is-active': distance === 70 }">70km</button>
            <button @click="distance = 100" :class="{ 'is-active': distance === 100 }">100km</button>
        </div>
        <div class="dscTxt">
            서울특별시 서초구 서초동에서 <strong>{{ distance }}</strong
            >Km 이내에 <strong>{{ data?.total }}</strong
            >개 스포츠클럽이 있습니다.
        </div>
    </div>
</template>

<style scoped lang="scss">
    .rgBtns {
        margin-bottom: 80px;
        &-tit {
            font-weight: 500;
            @include font-style(18);
            margin-right: 30px;
            i {
                position: relative;
                top: -2px;
                margin-right: 10px;
            }
        }
        button {
            margin-right: 12px;
            width: 110px;
            height: 40px;
            line-height: 40px;
            border-radius: 28px;
            border: solid 1px #dfe4e8;
            @include font-style(16);
            &.is-active {
                border: solid 1px #0fae6f;
                background-color: rgba(15, 174, 111, 0.05);
                color: #0fae6f;
                font-weight: 500;
            }
        }
    }

    %linebox {
        content: "";
        display: block;
        height: 6px;
        position: absolute;
        background: url("@/assets/imgs/png/back_gray_line.png") repeat-x;
        left: 0;
        width: 100%;
    }
    .dscTxt {
        @include font-style(18);
        color: #333;
        text-align: center;
        height: 80px;
        line-height: 80px;
        position: relative;
        margin-bottom: 60px;
        &::before {
            @extend %linebox;
            top: 0;
        }
        &::after {
            @extend %linebox;

            bottom: 0;
        }

        strong {
            &:first-child {
                color: #111;
            }
            &:last-child {
                color: #0fae6f;
            }
        }
    }
    .rg-clubList {
        background: #f4f9fb;
    }
</style>
